@import '../../../../styles/colors';

.Kalend__CalendarHeaderDates__col {
  display: flex;
  flex-direction: column;
  margin: 0;
  justify-content: center;
  align-items: center;
}

.Kalend__CalendarHeaderDates__circle {
  height: 32px;
  width: 32px;
  padding: 0;
  margin: 0;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $text;

  &-small {
    @extend .Kalend__CalendarHeaderDates__circle;
    margin-top: 2px;
    height: 20px;
    width: 20px;

    &-today {
      @extend .Kalend__CalendarHeaderDates__circle-small;
      background: $PRIMARY_LIGHT;

      &-dark {
        @extend .Kalend__CalendarHeaderDates__circle-small-today;
        background: $PRIMARY_DARK;
      }
    }
  }

  &-today {
    @extend .Kalend__CalendarHeaderDates__circle;
    background: $PRIMARY_LIGHT;

    &-dark {
      @extend .Kalend__CalendarHeaderDates__circle-today;
      background: $PRIMARY_DARK;
    }
  }
}


.Kalend__CalendarHeaderDates__text-size-small {
  font-size: 1.1em !important;
  padding: 0.4px;
}

.Kalend__CalendarHeaderDates__text {
  margin: 0;
  padding: 1px;
  font-size: 1.5em;
  align-self: center;
  text-align: center;
  font-weight: 500;
  color: $GRAY_800;

  &-dark {
    @extend .Kalend__CalendarHeaderDates__text;
    color: $GRAY_300;
  }

  &-today {
    @extend .Kalend__CalendarHeaderDates__text;
    color: $textDark !important;

    &-dark {
      @extend .Kalend__CalendarHeaderDates__text-today;
      color: $text;
    }
  }
}
